class Canny {
    constructor(canny) {
        this.canny = canny;
    }
    identify(appID, user, callback) {
        if (!this.canny) {
            return;
        }
        this.canny("identify", { appID, user }, callback);
    }
    authenticateCannyLink(url) {
        if (!this.canny) {
            return url;
        }
        return this.canny("authenticateCannyLink", url);
    }
    render(options) {
        if (!this.canny) {
            return;
        }
        this.canny("render", options);
    }
    initChangelog(options) {
        if (!this.canny) {
            return;
        }
        this.canny("initChangelog", options);
    }
    closeChangelog() {
        if (!this.canny) {
            return;
        }
        this.canny("closeChangelog");
    }
}
export { Canny };
